.spinner {
    width: 40px;
    height: 40px;
    position: absolute;
    display: flex;
    top: 50%;
    left: 50%;
    z-index: 16;
    transform: translate(-50%, -50%);

}

.preloader-bg {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    opacity: 1;
    z-index: 950;
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
}

.double-bounce1,
.double-bounce2 {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: $dark-blue-1;
    opacity: 0.6;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 18;
    -webkit-animation: sk-bounce 2.0s infinite ease-in-out;
    animation: sk-bounce 2.0s infinite ease-in-out;
}

.double-bounce2 {
    -webkit-animation-delay: -1.0s;
    animation-delay: -1.0s;
}

@-webkit-keyframes sk-bounce {
    0%,
    100% {
        -webkit-transform: scale(0.0)
    }
    50% {
        -webkit-transform: scale(1.0)
    }
}

@keyframes sk-bounce {
    0%,
    100% {
        transform: scale(0.0);
        -webkit-transform: scale(0.0);
    }
    50% {
        transform: scale(1.0);
        -webkit-transform: scale(1.0);
    }
}