.plyr__ads {
    border-radius: inherit;
    bottom: 0;
    cursor: pointer;
    left: 0;
    overflow: hidden;
    position: absolute;
    right: 0;
    top: 0;
    z-index: -1
}

.plyr__ads>div,
.plyr__ads>div iframe {
    height: 100%;
    position: absolute;
    width: 100%
}

.plyr__ads::after {
    background: rgba(47, 52, 61, .8);
    border-radius: 2px;
    bottom: 10px;
    color: #fff;
    content: attr(data-badge-text);
    font-size: 11px;
    padding: 2px 6px;
    pointer-events: none;
    position: absolute;
    right: 10px;
    z-index: 3
}

.plyr__ads::after:empty {
    display: none
}

.plyr__cues {
    background: currentColor;
    display: block;
    height: 4px;
    left: 0;
    margin: -2px 0 0;
    opacity: .8;
    position: absolute;
    top: 50%;
    width: 3px;
    z-index: 3
}

.plyr--no-transition {
    transition: none !important
}

.plyr__control--overlaid:focus{
    background-color: transparent;
}

.plyr__sr-only {
    clip: rect(1px, 1px, 1px, 1px);
    overflow: hidden;
    border: 0 !important;
    height: 1px !important;
    padding: 0 !important;
    position: absolute !important;
    width: 1px !important
}

.plyr [hidden] {
    display: none !important
} 




// Overwrite styles
.plyr--full-ui.plyr--video .plyr__control--overlaid {
    z-index: 3;
    cursor: pointer;
    display: block;
    font-size: 0px;
    width: 6rem;
    height: 6rem;
    background-image: url('../images/about-page/play_button_sm.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    @media (min-width: 1200px) {
        // background-image: url('../images/about-page/play_button_md.svg');
        &:hover {
            width: 8rem;
            height: 8rem;
            background-color: transparent;
            cursor: pointer;
            transition: background-size backrgound-image 1s ease-in-out;
            background-image: url('../images/about-page/play_button_md.svg');
        }
    }
    svg {
        display: none;
    }
}

.plyr__control[data-plyr="play"] {
    &:hover {
        background-color: transparent;
    }
}

.plyr__controls {
    display: none;
}

.plyr__poster {
    background-size: cover;
}

.plyr__control--overlaid {
    background: transparent;
}

.plyr__control.plyr__tab-focus {
    background: transparent;
    box-shadow: none;
}

.plyr--video .plyr__control.plyr__tab-focus {
    background: transparent;
