*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

html {
    font-size: 62.5%;
    // width:100vw;
}

input[type="text"] input[type="password"],
textarea, select{
  font-size: 16px !important;
}

input[type="text"]:focus, input[type="password"]:focus,
textarea:focus, select:focus {
  font-size: 16px !important;
}

body {
    box-sizing: border-box;
    overflow-x: hidden;
    // overflow-y: visible !important;
}

.errorInput{
    border-bottom: 1px solid red !important;
}

.validInput{
    border-bottom: 1px solid green !important;
}

.formValidatorTrue, .formValidatorFalse{
    font-family: $regular;
    color: $p-color-1;
    font-size: 1.2rem;
    width: 100%;
    display: flex;
    justify-content: center;
}

.scroll-blocker{
    overflow: hidden;
}

.display-block{
    display: block !important;
}

.display-none{
    display: none;
}

// .opacity-1{
//     opacity: 1 !important;
// }

// .opacity-0{
//     opacity: 0 !important;
// }

.z-10{
    z-index: 10;
}

#all-content{
    opacity: 0;
    transition: all 0.3s ease;
}

::selection {
    background: #010101;
    color: #fff;
    text-shadow: none;
}

.text-block__formatting *{
    color: $p-color-1;
    font-family: $light;
    font-size: 1.2rem;
    font-weight: 300;
    line-height: 2.8rem;
    letter-spacing: 0.33px;
    padding-bottom: 3rem;
    list-style-type: none;

    li{
        padding-bottom: 1rem;

        &:last-child{
            padding-bottom: 0;
        }
    }

    li::before{
        content: "•"; 
        color: $p-color-1;
        display: inline-block; 
        width: 1rem;
    }

    &:last-child{
        padding-bottom: 0;
    }
}

.mob-none {
    display: none;
}
.tab-none{
    display: none;
}

@media screen and (min-width:768px){
    .mob-none {
        display: block;
    }
}
@media screen and (min-width:1200px){

    .tab-none {
        display: block;
    }

    .text-block__formatting, .text-block__formatting *{
        letter-spacing: 0.39px;
        font-size: 1.4rem;
        padding-bottom: 3rem;
    }
}
